import make_count_vec from "./vectorize";
import labels from "./labels";
import DecisionTreeClassifier from "./DecisionTreeClassifier.min.js";

const vec_len = labels.length;
let clf = new DecisionTreeClassifier();

function search_label(string) {
  let res = [];
  for (let i = 0; i < labels.length; i++) {
    if (predict(labels[i], string) == 0) {
      res.push(labels[i]);
    }
  }
  return res;
}

function predict(string1, string2) {
  let features1 = make_count_vec(string1);
  let features2 = make_count_vec(string2);
  let features = features1.concat(features2);
  return clf.predict(features);
}

export default search_label;
